export default {
	props: {
		item: Object,
		index: Number
	},
	data() {
		return { loading: false };
	},
	methods: {
		// 查看电话
		getContact(id, type) {
			let params = {
				queriesType: type,
				id: id,
				selectType: 2
			};

			this.loading = true;
			this.$http.post(this.$urlconfig.getContact, params, {}).then(data => {
				this.loading = false;
				if (data.status === 0) {
					let lianxi = null;
					if (type === 1) {
						lianxi = ' 联系方式:' + data.data.contact;
					} else {
						lianxi = ' 联系邮箱:' + data.data.email;
					}
					uni.showModal({
						title: '请勿将联系方式提供给他人',
						content: '联系人:' + data.data.consigneeName + '; ' + lianxi,
						success: function(res) {
							if (res.confirm) {
								//  console.log('用户点击确定');
							} else if (res.cancel) {
								//  console.log('用户点击取消');
							}
						}
					});
				}
			});
		}
	}
};
